.container {
  border: 3px solid rgba(255, 255, 255, 0.289199);
  border-radius: 5px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 32px;
}

.score-display {
  background: linear-gradient(0deg, #f3f3f3 0%, #ffffff 100%);
  box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.196706);
  border-radius: 4px;
  margin: 12px;
  box-sizing: border-box;
  width: 10%;
}

.score-display p {
  font-family: "Barlow Semi Condensed";
  font-style: normal;
  font-weight: 600;
  font-size: 10px;
  line-height: 12px;
  text-align: center;
  letter-spacing: 1.5625px;
  color: #2a45c2;
  margin: 0;
}

.score-display h2 {
  font-family: "Barlow Semi Condensed";
  font-style: normal;
  font-weight: 700;
  font-size: 40px;
  line-height: 40px;
  text-align: center;
  color: #565468;
  margin: 0;
}
